import * as React from 'react';

export default class Blog extends React.Component {
    constructor(props: any) {
        super(props);
        let url = window.location.href;
        if (url.indexOf('second_middle_page') === -1) {
            window.location.replace(`${url}&second_middle_page`);
            if (url.indexOf('PAJK')) {
                // 跳转mall-flash
                window.location.href = 'http://www.test.pajkdc.com/mall-flash/index.html#/addresslist?app=PAJK&outBizType=PAJK';
            }
            else {
                // 跳转fillorderout
                window.location.href = 'http://www.test.pajkdc.com/fillorderout/index.html#/addresslist?app=PAJK&outBizType=PAJK';
            }
        }
        else {
            // 第二次进入到中间页
            window.history.go(-1);
        }
    }
    render() {
        return null;
    }
}